body, h1, h2, h3, h4, h5, h6, ul, p {
  margin: 0;
  padding: 0;
  list-style: none;
}

* {
  box-sizing: border-box;
}

h3, a {
  text-decoration: none;
  transition: color .3s;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-link {
  align-items: center;
  display: flex;
}

.header__nav {
  display: flex;
}

.header__nav-list {
  gap: 20px;
  padding: 0;
  display: flex;
}

.header__nav-item {
  display: flex;
}

.header__nav-link {
  color: #060b35;
  text-transform: uppercase;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  transition: color .3s;
  display: flex;
  position: relative;
}

.header__nav-link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.products {
  padding-block: 50px;
  padding-inline: 46px;
}

.products__list {
  grid-gap: 46px 48px;
  grid-template-columns: repeat(1, fit-content(100%));
  justify-content: center;
  justify-items: center;
  display: grid;
}

@media (width >= 488px) {
  .products__list {
    grid-template-columns: repeat(2, fit-content(100%));
  }
}

@media (width >= 768px) {
  .products__list {
    grid-template-columns: repeat(3, fit-content(100%));
  }
}

@media (width >= 1024px) {
  .products__list {
    grid-template-columns: repeat(4, fit-content(100%));
  }
}

.stars {
  border: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.product-card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  gap: 16px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s, color .3s;
  display: flex;
}

.product-card__info-content {
  flex-direction: column;
  align-items: center;
  gap: 40px;
  display: flex;
}

.product-card__image {
  width: 160px;
  height: 134px;
  display: block;
}

.product-card__info {
  height: auto;
  margin: 0;
  padding: 0;
}

.product-card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card:hover .product-card__title {
  color: #34568b;
}

.product-card__code {
  color: #616070;
  font-size: 10px;
  line-height: 14px;
}

.product-card__rate {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.product-card__reviews-count {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 8px;
  display: flex;
}

.product-card__price-title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.product-card__price-count {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__buy-link {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  box-sizing: border-box;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: background-color .3s;
  display: flex;
}

.product-card__buy-link:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.page__body {
  margin: 0;
  font-family: Roboto, Helvetica, sans-serif;
}

